/* App.css */

body {
  font-family: Arial, sans-serif; /* Use a readable font */
  background-color: #f8f9fa; /* Set a light background color */
  margin: 0;
  padding: 0;
}

.container {
  max-width: 800px;
  margin: 50px auto; /* Center the container vertically */
  padding: 20px;
  background-color: #fff; /* Add a white background */
  border-radius: 10px; /* Add rounded corners */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Add a subtle shadow */
}

h1 {
  color: #343a40; /* Set heading color */
  text-align: center; /* Center the text */
  margin-bottom: 20px; /* Add space below the heading */
}

.description {
  font-size: 18px;
  color: #6c757d; /* Set text color */
  margin-bottom: 20px; /* Add space below the description */
}

.category {
  margin-bottom: 30px; /* Add space between categories */
}

.preview-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start; /* Align items to the start */
  padding: 20px;
  background-color: #f8f9fa; /* Set background color */
  border-radius: 10px; /* Add rounded corners */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Add a subtle shadow */
  position: relative; /* Add position relative */
}

.preview {
  flex: 1;
  max-width: calc(100% - 150px); /* Limit width to leave space for button */
}

.link {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  position: absolute; /* Add position absolute */
  right: 0; /* Align to the right */
  top: 50%; /* Center vertically */
  transform: translateY(-50%); /* Adjust vertical position */
  font-weight: bold;
}

.link:hover {
  background-color: #0056b3;
  transform: translateY(-50%) translateX(2px); /* Move slightly right on hover */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* Add a subtle shadow on hover */
}